import React, { useReducer, useContext, useEffect } from "react";

import socketIOClient from "socket.io-client";
import socketReducer from "./socketReducer";
import SocketContext from "./SocketContext";
import axios from "axios";
import NotificationsContext from "../notifications/NotificationsContext";

import AuthContext from "../auth/authContext";
import DriversContext from "../drivers/DriversContext";
import url from "../../core/urls";

const SocketState = (props) => {
  const { getAds, getNotifications, setNewOrder } = useContext(
    NotificationsContext
  );
  const { isAuthenticated } = useContext(AuthContext);
  const { setDrivers, setModes, modes } = useContext(DriversContext);
  const initialState = {
    socket: null,
  };
  const [state, dispatch] = useReducer(socketReducer, initialState);

  useEffect(() => {
    const socket = socketIOClient(url.socket_url);
    socket.on("drivers", (drivers) => {
      setDrivers(drivers);
    });
    socket.on("modes", setModes);
    socket.emit("getDrivers");
    dispatch({
      type: "CREATE_SOCKET",
      payload: socket,
    });
    socket.on("NewDelivery", setNewOrder);
    // eslint-disable-next-line
  }, []);

  const switchModes = (mode) => {
    const newModes = (modes.includes(mode)
      ? modes.filter((m) => m !== mode)
      : [...modes, mode]
    ).sort((a, b) => {
      const numberMatrix = ["Motorcycle", "Car", "Mini Van"];
      return numberMatrix.indexOf(a) - numberMatrix.indexOf(b);
    });
    state.socket?.emit("switchModes", newModes);
  };

  useEffect(() => {
    if (state.socket) {
      try {
        axios.post("/api/notifications/socket", { id: state.socket.id });
      } catch (error) {
        console.log(error);
      }
      state.socket.on("connect", async () => {
        console.log(
          "I connected...",
          state.socket.id,
          new Date().toLocaleTimeString()
        );
      });
      state.socket.on("NewNotification", () => {
        getNotifications();
      });
      state.socket.on("Ads", getAds);
    }
    //eslint-disable-next-line
  }, [state.socket, isAuthenticated]);

  return (
    <SocketContext.Provider value={{ socket: state.socket, switchModes }}>
      {props.children}
    </SocketContext.Provider>
  );
};
export default SocketState;
