import { DRIVERS_LOCATION, SET_MODES } from "../types";

const DriversReducer = (state, action) => {
  switch (action.type) {
    case DRIVERS_LOCATION:
      return { ...state, ...action.payload };
    case SET_MODES:
      return { ...state, modes: action.payload };
    default:
      return state;
  }
};
export default DriversReducer;
