import React from "react";
import clsx from "clsx";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";

import Charts from "../components/core/Charts";

import { makeStyles } from "@material-ui/core/styles";
import axios from "axios";
import authContext from "../components/context/auth/authContext";
import { groupByDay, groupByMonth } from "../components/core/utils";

const useStyles = makeStyles((theme) => ({
  card: {
    width: "100%",
  },
  primary: {
    backgroundColor: theme.palette.primary.main,
  },
  primaryText: {
    color: theme.palette.primary.contrastText,
  },
}));

const Analytics = () => {
  const classes = useStyles();
  const { loadUser } = React.useContext(authContext);
  const [sales, setSales] = React.useState({ deliveries: 0, cancelled: 0 });
  const [revenue, setRevenue] = React.useState({ deliveries: 0, cancelled: 0 });
  const [chartData, setChartData] = React.useState([]);
  const [barData, setBarData] = React.useState([]);
  const [monthSales, setMonthSales] = React.useState([]);
  const [monthRev, setMonthRev] = React.useState([]);
  const [weekSales, setWeekSales] = React.useState([]);
  const [weekRev, setWeekRev] = React.useState([]);
  React.useEffect(() => {
    (async () => {
      try {
        loadUser();
        const res = await axios.get("/api/admin/performance");
        const data = res.data;
        // mini cards
        const deliveries = data.day.find(
          (info) => info._id === "Deliveries"
        ) || { total: 0, revenue: 0 };
        const cancelled = data.day.find((info) => info._id === "Cancelled") || {
          total: 0,
          revenue: 0,
        };
        setSales({
          deliveries: deliveries.total,
          cancelled: cancelled.total,
        });
        setRevenue({
          deliveries: deliveries.revenue,
          cancelled: cancelled.revenue,
        });
        // week chart
        setChartData(groupByDay(data.weekly));
        setBarData(groupByMonth(data.monthly));
      } catch (err) {
        console.log(err);
      }
    })();
    // eslint-disable-next-line
  }, []);

  React.useEffect(() => {
    setWeekSales(chartData.map((data) => data[0]));
    setWeekRev(chartData.map((data) => data[1]));
    setMonthRev(barData.map((data) => data[1]));
    setMonthSales(barData.map((data) => data[0]));
  }, [chartData, barData]);

  return (
    <React.Fragment>
      <Box display="flex" flexDirection="row" mb={3}>
        <Typography variant="h5" color="secondary" gutterBottom>
          Welcome Back,
        </Typography>
        <Box width={10} />
        <Typography variant="h5" color="initial" gutterBottom>
          Admin
        </Typography>
      </Box>
      <Grid container spacing={5}>
        <Grid item xs={4}>
          <Typography variant="subtitle1" color="inherit">
            Today
          </Typography>
        </Grid>
        <Grid item xs={4}>
          <Typography variant="subtitle1" color="inherit">
            This week
          </Typography>
        </Grid>
        <Grid item xs={4}>
          <Typography variant="subtitle1" color="inherit">
            Monthly
          </Typography>
        </Grid>
        <Grid item xs={12} md={4}>
          <Paper className={clsx([classes.card, classes.primary])}>
            <Box p={2}>
              <Typography
                variant="h6"
                color="textPrimary"
                className={classes.primaryText}
              >
                Sales
              </Typography>
              <Box height={30} />
              <Typography
                variant="body1"
                color="textSecondary"
                className={classes.primaryText}
              >
                {`${sales.deliveries} Deliveries`}
              </Typography>
              <Typography
                variant="body1"
                color="textSecondary"
                className={classes.primaryText}
              >
                {`${sales.cancelled} Cancelled`}
              </Typography>
            </Box>
          </Paper>
        </Grid>
        <Grid item xs={12} md={4}>
          <Charts
            data={weekSales}
            argument="date"
            lineValues={["cancelled", "sales"]}
          />
        </Grid>
        <Grid item xs={12} md={4}>
          <Charts
            data={monthSales}
            argument="date"
            barValues={["cancelled", "sales"]}
          />
        </Grid>
        <Grid item xs={12} md={4}>
          <Paper className={classes.card}>
            <Box p={2}>
              <Typography variant="h6" color="secondary">
                Revenue
              </Typography>
              <Box height={30} />
              <Typography variant="body1" color="inherit">
                {`${revenue.deliveries} Deliveries`}
              </Typography>
              <Typography variant="body1" color="inherit">
                {`${revenue.cancelled} Cancelled`}
              </Typography>
            </Box>
          </Paper>
        </Grid>
        <Grid item xs={12} md={4}>
          <Charts
            data={weekRev}
            argument="date"
            lineValues={["cancelled", "sales"]}
          />
        </Grid>
        <Grid item xs={12} md={4}>
          <Charts
            data={monthRev}
            argument="date"
            barValues={["cancelled", "sales"]}
          />
        </Grid>
      </Grid>
    </React.Fragment>
  );
};

export default Analytics;
