import React, { useReducer } from "react";

import { DRIVERS_LOCATION, SET_MODES } from "../types";
import DriversReducer from "./DriversReducer";
import DriversContext from "./DriversContext";

const DriversState = (props) => {
  const initialState = {
    drivers: {},
    markers: [],
    modes: [],
  };
  const [state, dispatch] = useReducer(DriversReducer, initialState);

  const setDrivers = (drivers) => {
    const markers = Object.keys(drivers).map((_id) => {
      return { ...drivers[_id], _id };
    });
    dispatch({
      type: DRIVERS_LOCATION,
      payload: { markers, drivers },
    });
  };

  const setModes = (modes) => {
    dispatch({
      type: SET_MODES,
      payload: modes,
    });
  };

  return (
    <DriversContext.Provider
      value={{
        drivers: state.drivers,
        markers: state.markers,
        modes: state.modes,
        setDrivers,
        setModes,
      }}
    >
      {props.children}
    </DriversContext.Provider>
  );
};

export default DriversState;
