import React, { useState } from "react";
import { EditingState } from "@devexpress/dx-react-grid";
import {
  Grid,
  Table,
  TableHeaderRow,
  TableEditColumn,
  TableInlineCellEditing,
} from "@devexpress/dx-react-grid-material-ui";
import {
  Box,
  DialogActions,
  Button,
  DialogContent,
  Dialog,
} from "@material-ui/core";
import Axios from "axios";
import AlertContext from "../context/alert/alertContext";
import { useContext } from "react";

const getRowId = (row) => row.id;

const FocusableCell = ({ onClick, ...restProps }) => (
  <Table.Cell {...restProps} tabIndex={0} onFocus={onClick} />
);

const EditPrice = ({
  columns,
  rowsStart,
  columnExtensions,
  onClose,
  open,
  mode,
  setMatrix,
}) => {
  const [rows, setRows] = useState([]);
  const [editingCells, setEditingCells] = useState([]);
  const [loading, setLoading] = useState(false);
  const { setAlert } = useContext(AlertContext);

  React.useEffect(() => {
    if (Array.isArray(rowsStart) && open) {
      setRows(
        rowsStart.map((row, index) => {
          return { ...row, id: row.id || index };
        })
      );
    } else {
      setRows([]);
    }
    // eslint-disable-next-line
  }, [open]);

  const update = async () => {
    setLoading(true);
    try {
      const priceList = rows
        .map((row) => {
          const price = Number(row.price);
          const distance = Number(row.distance);
          if (isNaN(distance) || isNaN(price)) {
            throw Error("Invalid Price or Distance value");
          }
          return { id: row.id, price, distance };
        })
        .sort((a, b) => a.distance - b.distance);
      await Axios.post("/api/admin/price", { priceList, mode });
      setMatrix(priceList);
      onClose();
    } catch (err) {
      console.log(err);
      setAlert("Please review Price List", "error");
    }
    setLoading(false);
  };

  const commitChanges = ({ added, changed, deleted }) => {
    let changedRows;
    if (added) {
      const startingAddedId =
        rows.length > 0
          ? Math.max(rows[rows.length - 1].id, rows[0].id) + 1
          : 0;
      changedRows = [
        ...added.map((row, index) => ({
          id: startingAddedId + index,
          ...row,
        })),
        ...rows,
      ];
      setEditingCells([
        { rowId: startingAddedId, columnName: columns[0].name },
      ]);
    }
    if (changed) {
      changedRows = rows.map((row) =>
        changed[row.id] ? { ...row, ...changed[row.id] } : row
      );
    }
    if (deleted) {
      const deletedSet = new Set(deleted);
      changedRows = rows.filter((row) => !deletedSet.has(row.id));
    }
    setRows(changedRows);
  };

  const addEmptyRow = () => commitChanges({ added: [{}] });

  return (
    <Dialog open={open} onClose={onClose}>
      <DialogContent>
        <Box minHeight={500}>
          <Grid rows={rows} columns={columns} getRowId={getRowId}>
            <EditingState
              onCommitChanges={commitChanges}
              editingCells={editingCells}
              onEditingCellsChange={setEditingCells}
              addedRows={[]}
              onAddedRowsChange={addEmptyRow}
            />
            <Table
              cellComponent={FocusableCell}
              columnExtensions={columnExtensions}
            />
            <TableHeaderRow />
            <TableInlineCellEditing selectTextOnEditStart />
            <TableEditColumn showAddCommand showDeleteCommand />
          </Grid>
        </Box>
      </DialogContent>
      <DialogActions>
        <Button color="primary" onClick={update} disabled={loading}>
          Submit
        </Button>
        <Button color="default" onClick={onClose} disabled={loading}>
          Cancel
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default EditPrice;
