import React from "react";
import Box from "@material-ui/core/Box";
import TabTheme from "../components/core/TabTheme";
import Typography from "@material-ui/core/Typography";
import MyVirtualTable from "../components/core/VirtualTableTheme";

import { DataTypeProvider } from "@devexpress/dx-react-grid";
import Axios from "axios";
import NotificationsContext from "../components/context/notifications/NotificationsContext";

const rows = [];
for (let index = 0; index < 100; index++) {
  rows.push({
    id: index,
    dateCreated: new Date().toISOString(),
    client: "Samuel Seibidor",
    pickUp: "10, Wogu Street D/Line, Port Harcourt",
    dropOff: "Creekside Logistics, Port Harcourt",
    distance: 5,
    mode: "Motorcycle",
  });
}

const columns = [
  { name: "dateCreated", title: "Date Created" },
  { name: "client", title: "Client" },
  { name: "pickUp", title: "Pick-up" },
  { name: "dropOff", title: "Drop-off" },
  { name: "distance", title: "Distance (km)" },
  { name: "mode", title: "Mode" },
];

const columnExtensions = [
  { columnName: "distance", width: 120, align: "center" },
];

const DateFormatter = ({ value }) => new Date(value).toLocaleDateString();

const DateFormatProvider = (props) => (
  <DataTypeProvider formatterComponent={DateFormatter} {...props} />
);

const Deliveries = (props) => {
  const [pending, setPending] = React.useState([]);
  const [processing, setProcessing] = React.useState([]);
  const [delivered, setDelivered] = React.useState([]);
  const [cancelled, setCancelled] = React.useState([]);
  const [display, setDisplay] = React.useState([]);
  const [tab, setTab] = React.useState(0);
  const [loading, setLoading] = React.useState(false);

  const { newOrder, clearNewOrder } = React.useContext(NotificationsContext);

  React.useEffect(() => {
    _getDeliveries();
    clearNewOrder();
    // eslint-disable-next-line
  }, []);

  React.useEffect(() => {
    if (newOrder) {
      _getDeliveries();
      setTimeout(clearNewOrder, 10000);
    }
  }, [newOrder, clearNewOrder]);

  const _getDeliveries = async () => {
    setLoading(true);
    try {
      const res = await Axios.get("/api/admin/deliveries");
      const deliveries = res.data;
      setPending(
        deliveries.filter((delivery) => delivery.status === "Pending")
      );
      setProcessing(
        deliveries.filter((delivery) => delivery.status === "Processing")
      );
      setCancelled(
        deliveries.filter((delivery) => delivery.status === "Cancelled")
      );
      setDelivered(
        deliveries.filter((delivery) => delivery.status === "Delivered")
      );
    } catch (err) {
      console.log(err);
    }
    setLoading(false);
  };
  const adapt = (data) => {
    const displayRows = data.map((row) => {
      const { dateCreated, client, from, to, distance, mode, _id } = row;
      const pickUp = from && from.address;
      const dropOff = to && to.address;
      const { name } = client || { name: "" };
      return {
        dateCreated,
        client: name,
        pickUp,
        dropOff,
        distance,
        mode,
        _id,
      };
    });
    return displayRows;
  };
  React.useEffect(() => {
    switch (tab) {
      case 0:
        setDisplay(adapt(pending));
        break;
      case 1:
        setDisplay(adapt(processing));
        break;
      case 2:
        setDisplay(adapt(delivered));
        break;
      case 3:
        setDisplay(adapt(cancelled));
        break;
      default:
        break;
    }
  }, [tab, loading, pending, processing, delivered, cancelled]);

  return (
    <React.Fragment>
      <Box display="flex" flexDirection="row" mb={3}>
        <Typography variant="h5" color="initial" gutterBottom>
          Deliveries
        </Typography>
      </Box>
      <TabTheme
        aria="deliveries tab"
        tabs={["Pending", "Processing", "Delivered", "Cancelled"]}
        onChange={(value) => setTab(value)}
      />
      <MyVirtualTable
        rows={display}
        columns={columns}
        getRowId={(row) => row._id}
        columnExtensions={columnExtensions}
        formatters={[<DateFormatProvider for={["dateCreated"]} />]}
        onClick={(id) => {
          props.history.push(`/dashboard/delivery/${id}`);
        }}
        loading={loading}
      />
    </React.Fragment>
  );
};

export default Deliveries;
