import React from "react";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import TextField from "../components/form/TextField";
import AuthBackground from "../components/core/AuthBackground";
import { DatePicker } from "@material-ui/pickers";

import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";
import { IconButton, InputAdornment } from "@material-ui/core";

import { Link } from "react-router-dom";
import AlertContext from "../components/context/alert/alertContext";
import {
  phoneValidator,
  emailValidator,
  passwordValidator,
  birthValidator,
} from "../components/core/utils";
import AuthContext from "../components/context/auth/authContext";

const Register = (props) => {
  const { setAlert } = React.useContext(AlertContext);
  const { register, error, isAuthenticated } = React.useContext(AuthContext);
  const [selectedDate, handleDateChange] = React.useState(null);
  const [name, setName] = React.useState("");
  const [email, setEmail] = React.useState("");
  const [password, setPassword] = React.useState("");
  const [phone, setPhone] = React.useState("");
  const [secret, setSecret] = React.useState("");

  const [passwordVisible, setPasswordVisible] = React.useState(false);

  React.useEffect(() => {
    isAuthenticated && props.history.push("/login");
  }, [isAuthenticated, props.history]);

  const _register = async () => {
    const nameError = name === "" ? "Please fill in your full name" : "";
    const emailError = emailValidator(email);
    const passwordError = passwordValidator(password);
    const phoneError = phoneValidator(phone);
    const secretError =
      secret === "" ? "Please fill in the registration secret" : "";
    const birthError = birthValidator(selectedDate);
    if (
      nameError ||
      emailError ||
      passwordError ||
      phoneError ||
      secretError ||
      birthError
    ) {
      setAlert(
        [
          nameError,
          emailError,
          phoneError,
          passwordError,
          secretError,
          birthError,
        ].find((error) => error),
        "error"
      );
    } else {
      register({
        name,
        email,
        password,
        phone,
        birthday: selectedDate,
        secret,
      });
    }
  };

  React.useEffect(() => {
    error && setAlert(error, "error");
  }, [error, setAlert]);
  return (
    <Box width={1} display="flex" flexDirection="row" height={1}>
      <Box
        width={800}
        alignItems="center"
        justifyContent="center"
        height={1}
        display="flex"
        flexDirection="column"
      >
        <Box height={0.25} />
        <Box p={5} m={0} width={0.8}>
          <Box display="flex" justifyContent="center">
            <Typography
              variant="h4"
              color="primary"
              alignText="center"
              gutterBottom
            >
              Create an Account
            </Typography>
          </Box>
          <TextField
            id="outlined-basic"
            label="Name"
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
          <DatePicker
            variant="inline"
            inputVariant="outlined"
            label="Birthday"
            value={selectedDate}
            onChange={handleDateChange}
            fullWidth
            views={["month", "date", "year"]}
          />
          <TextField
            id="outlined-basic"
            label="Email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <Box display="flex" flexDirection="row">
            <TextField
              id="outlined-basic"
              label="Phone"
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
            />
            <TextField
              id="outlined-basic"
              label="Password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              type={passwordVisible ? "text" : "password"}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton
                      onClick={() => setPasswordVisible(!passwordVisible)}
                    >
                      {passwordVisible ? <Visibility /> : <VisibilityOff />}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
            />
          </Box>
          <TextField
            id="outlined-basic"
            label="Secret"
            value={secret}
            onChange={(e) => setSecret(e.target.value)}
          />
          <Button
            variant="contained"
            color="primary"
            size="large"
            onClick={_register}
            fullWidth
          >
            Register
          </Button>
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="flex-end"
          height={0.15}
        >
          <Typography
            variant="body2"
            color="textPrimary"
            align="right"
            style={{ margin: 8, textDecoration: "none" }}
            component={Link}
            to="/login"
          >
            Back to Login
          </Typography>
        </Box>
      </Box>
      <AuthBackground />
    </Box>
  );
};

export default Register;
