import React, { useReducer } from "react";
import NotificationsContext from "./NotificationsContext";
import notificationsReducer from "./notificationsReducer";

import axios from "axios";

import {
  GET_ALL_NOTIFICATIONS,
  CLEAR_NEW_NOTIFICATIONS,
  NOTIFICATION_ERROR,
  ADS,
  READ_NOTIFICATION,
  READ_ALL_NOTIFICATIONS,
  NEW_ORDER,
  CLEAR_NEW_ORDER,
} from "../types";

import Axios from "axios";

const NotificationsState = (props) => {
  const initialState = {
    notifications: [],
    newNotifications: false,
    currentNotification: null,
    ads: [],
    newOrder: true,
  };
  const [state, dispatch] = useReducer(notificationsReducer, initialState);

  const getAds = async () => {
    try {
      const res = await Axios.get("api/users/ads");
      dispatch({ type: ADS, payload: res.data });
    } catch (err) {
      console.log(err);
    }
  };

  const getNotifications = async () => {
    try {
      const res = await axios.get("/api/notifications");
      dispatch({
        type: GET_ALL_NOTIFICATIONS,
        payload: res.data,
      });
    } catch (err) {
      dispatch({
        type: NOTIFICATION_ERROR,
        payload:
          (err.response &&
            ((err.response.data && err.response.data.msg) ||
              err.response.statusText)) ||
          "Runtime Error",
      });
    }
  };

  const clearNewNotifications = () => {
    dispatch({ type: CLEAR_NEW_NOTIFICATIONS });
  };

  const readNotification = async (id) => {
    try {
      const res = await axios.post(`/api/notifications/read/${id}`);
      dispatch({ type: READ_NOTIFICATION, payload: res.data });
    } catch (err) {
      console.log(err);
    }
  };
  const readAllNotifications = async () => {
    try {
      const res = await axios.post(`/api/notifications/read_all`);
      dispatch({ type: READ_ALL_NOTIFICATIONS, payload: res.data });
    } catch (err) {
      console.log(err);
      dispatch({
        type: NOTIFICATION_ERROR,
        payload:
          (err.response &&
            ((err.response.data && err.response.data.msg) ||
              err.response.statusText)) ||
          "Runtime Error",
      });
    }
  };

  const setNewOrder = () => dispatch({ type: NEW_ORDER });
  const clearNewOrder = () => dispatch({ type: CLEAR_NEW_ORDER });

  return (
    <NotificationsContext.Provider
      value={{
        notifications: state.notifications,
        currentNotification: state.currentNotification,
        newNotifications: state.newNotifications,
        ads: state.ads,
        newOrder: state.newOrder,
        readNotification,
        readAllNotifications,
        getNotifications,
        clearNewNotifications,
        getAds,
        setNewOrder,
        clearNewOrder,
      }}
    >
      {props.children}
    </NotificationsContext.Provider>
  );
};

export default NotificationsState;
