import "./App.css";
import React, { useContext } from "react";

import { MuiPickersUtilsProvider } from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  Link,
  useHistory,
} from "react-router-dom";

import CloseRoundedIcon from "@material-ui/icons/CloseRounded";

import Login from "./pages/Login";
import Register from "./pages/Register";
import ForgotPassword from "./pages/ForgotPassword";
import Analytics from "./pages/Analytics";
import DashboardWrapper from "./components/core/DashboardWrapper";
import Deliveries from "./pages/Deliveries";
import DeliveryInfo from "./pages/DeliveryInfo";
import Drivers from "./pages/Drivers";
import DriverInfo from "./pages/DriverInfo";
import Pricing from "./pages/Pricing";

import { createMuiTheme } from "@material-ui/core/styles";
import { ThemeProvider } from "@material-ui/styles";
import red from "@material-ui/core/colors/red";
import pink from "@material-ui/core/colors/pink";
import "./components/core/socket";
import DriversState from "./components/context/drivers/DriversState";
import Users from "./pages/Users";
import Alerts from "./components/context/alert/Alerts";
import AllStates from "./components/context/AllStates";
import {
  Box,
  Paper,
  Typography,
  Slide,
  Button,
  IconButton,
} from "@material-ui/core";
import NotificationsContext from "./components/context/notifications/NotificationsContext";
import PrivateRoute from "./components/routing/PrivateRoute";
import Ads from "./pages/Ads";

const theme = createMuiTheme({
  palette: {
    primary: { main: red[800] },
    secondary: { main: pink.A400 },
    action: { focus: red[500], selected: red[700], hover: red[100] },
  },
});

function App() {
  return (
    <DriversState>
      <ThemeProvider theme={theme}>
        <Alerts>
          <AllStates>
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <Router>
                <Switch>
                  <Route exact path="/login" component={Login} />
                  <Route exact path="/register" component={Register} />
                  <Route exact path="/forgot" component={ForgotPassword} />
                  <Route path="/dashboard">
                    <DashboardWrapper>
                      <PrivateRoute
                        exact
                        path="/dashboard/deliveries"
                        component={Deliveries}
                      />
                      <PrivateRoute
                        exact
                        path="/dashboard/drivers"
                        component={Drivers}
                      />
                      <PrivateRoute
                        exact
                        path="/dashboard/users"
                        component={Users}
                      />
                      <Route
                        exact
                        path="/dashboard/analytics"
                        component={Analytics}
                      />
                      <PrivateRoute
                        exact
                        path="/dashboard/pricing"
                        component={Pricing}
                      />
                      <PrivateRoute
                        exact
                        path="/dashboard/delivery/:id"
                        component={DeliveryInfo}
                      />
                      <PrivateRoute
                        exact
                        path="/dashboard/driver/:id"
                        component={DriverInfo}
                      />
                      <PrivateRoute
                        exact
                        path="/dashboard/ads"
                        component={Ads}
                      />
                      <Prompt />
                    </DashboardWrapper>
                  </Route>
                  <Redirect from="/" to="/login" />
                </Switch>
              </Router>
            </MuiPickersUtilsProvider>
          </AllStates>
        </Alerts>
      </ThemeProvider>
    </DriversState>
  );
}

const Prompt = (props) => {
  const history = useHistory();
  const { newOrder, clearNewOrder } = useContext(NotificationsContext);
  return (
    <Slide direction="left" in={newOrder} mountOnEnter unmountOnExit>
      <Box
        position="absolute"
        bottom={50}
        right={100}
        zIndex="modal"
        onClick={() => history.push("/dashboard/deliveries")}
      >
        <Paper elevation={4}>
          <Box
            p={2}
            width={320}
            alignItems="center"
            justifyContent="space-between"
            display="flex"
            bgcolor="secondary.main"
            color="secondary.contrastText"
          >
            <img
              src="https://www.flaticon.com/svg/static/icons/svg/891/891012.svg"
              height={50}
              width={50}
              alt="Alarm bell"
            />
            <Typography variant="h6" color="inherit">
              New order notice
            </Typography>
            <IconButton
              onClick={(e) => {
                e.stopPropagation();
                clearNewOrder();
              }}
            >
              <CloseRoundedIcon />
            </IconButton>
          </Box>
        </Paper>
      </Box>
    </Slide>
  );
};

export default App;
