import React, { useEffect, useState } from "react";
import {
  Box,
  Typography,
  Paper,
  IconButton,
  Link,
  TextField,
  DialogContent,
  DialogTitle,
  Dialog,
  DialogActions,
  Button,
  Grid,
  InputAdornment,
} from "@material-ui/core";
import { DataGrid } from "@material-ui/data-grid";
import Axios from "axios";

import CheckCircleRoundedIcon from "@material-ui/icons/CheckCircleRounded";
import CancelRoundedIcon from "@material-ui/icons/CancelRounded";
import AttachFileRoundedIcon from "@material-ui/icons/AttachFileRounded";
import { DatePicker } from "@material-ui/pickers";

const blank = {
  title: "",
  link: "",
  image: null,
  expires: null,
};

const Ads = () => {
  const [ads, setAds] = useState([]);
  const [newAd, setNewAd] = useState(blank);
  const [openAd, setOpenAd] = useState(false);
  const [uploadingAd, setUploadingAd] = useState(false);

  const closeAd = () => {
    setOpenAd(false);
    setNewAd(blank);
  };
  const getAds = async () => {
    try {
      const res = await Axios.get("/api/admin/ads");
      setAds(
        res.data.map((ad) => {
          return { id: ad._id, ...ad };
        })
      );
      console.log(res.data);
    } catch (err) {
      console.log(err);
    }
  };
  const switchValid = async (id) => {
    try {
      const res = await Axios.post(`/api/admin/ads/valid/${id}`);
      setAds(
        ads.map((ad) => (ad._id !== id ? ad : { ...ad, valid: res.data.valid }))
      );
    } catch (err) {
      console.log(err);
    }
  };
  const uploadAd = async (id) => {
    setUploadingAd(true);
    const { title, link, image, expires } = newAd;
    if (!title) {
      alert("Please input a title");
    } else if (!image) {
      alert("Please attach an image file");
    } else {
      try {
        const formData = new FormData();
        const cloud = process.env.REACT_APP_CLOUD;
        formData.append("file", image);
        formData.append("upload_preset", process.env.REACT_APP_UPLOAD_PRESET);
        formData.append("cloud_name", cloud);
        formData.append("folder", process.env.REACT_APP_FOLDER);
        const upload_url = `https://api.cloudinary.com/v1_1/${cloud}/auto/upload`;
        await fetch(upload_url, {
          method: "POST",
          body: formData,
        })
          .then((res) => res.json())
          .then(async (response) => {
            await Axios.post("/api/admin/ads", {
              title,
              link,
              image: response.secure_url,
              expires,
            });
            getAds();
            closeAd();
          });
      } catch (err) {
        console.log(err);
        alert("An error occured while uploading this ad");
      }
    }
    setUploadingAd(false);
  };
  useEffect(() => {
    getAds();
  }, []);
  return (
    <React.Fragment>
      <Box display="flex" flexDirection="row" mb={3}>
        <Typography variant="h5" color="initial" gutterBottom>
          Advertisement
        </Typography>
        <Box ml={5}>
          <Button
            variant="outlined"
            color="default"
            onClick={() => setOpenAd(true)}
          >
            Create
          </Button>
        </Box>
      </Box>
      <Box>
        <Paper>
          <Box height={700} width={1}>
            <DataGrid
              columns={[
                {
                  field: "image",
                  headerName: "Image",
                  width: 300,
                  renderCell: (params) => (
                    <a href={params.value} rel="noreferrer" target="_blank">
                      <img
                        width={250}
                        height={250}
                        src={params.value}
                        alt={params.rowIndex}
                      />
                    </a>
                  ),
                },
                { field: "title", headerName: "Title", width: 300 },
                {
                  field: "link",
                  headerName: "Link",
                  width: 700,
                  renderCell: (params) => (
                    <Link href={params.value} target="_blank">
                      {params.value}
                    </Link>
                  ),
                },
                {
                  field: "dateUploaded",
                  headerName: "Created",
                  width: 150,
                  valueFormatter: (params) =>
                    new Date(params.value).toLocaleDateString(),
                },
                {
                  field: "expires",
                  headerName: "Expires",
                  width: 150,
                  valueFormatter: (params) =>
                    params.value
                      ? new Date(params.value).toLocaleDateString()
                      : "",
                },
                {
                  field: "valid",
                  headerName: "Valid",
                  width: 150,
                  renderCell: (params) => (
                    <IconButton
                      color={params.value ? "primary" : "default"}
                      onClick={() => switchValid(params.row.id)}
                    >
                      {params.value ? (
                        <CheckCircleRoundedIcon />
                      ) : (
                        <CancelRoundedIcon />
                      )}
                    </IconButton>
                  ),
                },
              ]}
              rows={ads}
              rowHeight={100}
            />
          </Box>
        </Paper>
        <Dialog
          open={openAd}
          onClose={!uploadingAd && closeAd}
          aria-labelledby=""
        >
          <DialogTitle>Add Advert</DialogTitle>
          <DialogContent>
            <Box width={500}>
              <Grid container spacing={3} display="flex" direction="column">
                <Grid item>
                  <TextField
                    label="Title"
                    variant="standard"
                    value={newAd.title}
                    onChange={(e) =>
                      setNewAd({ ...newAd, title: e.currentTarget.value })
                    }
                    fullWidth
                  />
                </Grid>
                <Grid item>
                  <TextField
                    label="Link"
                    variant="standard"
                    value={newAd.link}
                    onChange={(e) =>
                      setNewAd({ ...newAd, link: e.currentTarget.value })
                    }
                    fullWidth
                  />
                </Grid>
                <Grid item>
                  <Box flex flexDirection="row">
                    <TextField
                      label="File"
                      variant="standard"
                      disabled
                      value={newAd.image ? newAd.image.name : ""}
                      onChange={(e) => {}}
                      fullWidth
                      InputProps={{
                        endAdornment: (
                          <InputAdornment position="end">
                            <input
                              id="file-input"
                              type="file"
                              accept="image/*"
                              style={{ display: "none" }}
                              onChange={(e) => {
                                setNewAd({
                                  ...newAd,
                                  image: e.target.files[0],
                                });
                              }}
                            />
                            <label htmlFor="file-input">
                              <IconButton component="span" color="primary">
                                <AttachFileRoundedIcon />
                              </IconButton>
                            </label>
                          </InputAdornment>
                        ),
                      }}
                    />
                  </Box>
                </Grid>
                <Grid item>
                  <DatePicker
                    variant="dialog"
                    inputVariant="standard"
                    label="Expires"
                    value={newAd.expires}
                    minDate={new Date()}
                    onChange={(expires) => setNewAd({ ...newAd, expires })}
                    fullWidth
                    views={["month", "date", "year"]}
                    format="yyyy/MM/dd"
                  />
                </Grid>
              </Grid>
            </Box>
          </DialogContent>
          <DialogActions>
            <Button onClick={uploadAd} disabled={uploadingAd} color="primary">
              Add
            </Button>
            <Button onClick={closeAd} disabled={uploadingAd} color="default">
              Cancel
            </Button>
          </DialogActions>
        </Dialog>
      </Box>
    </React.Fragment>
  );
};

export default Ads;
