import React from "react";
import bounce from "react-animations/lib/bounce";
import Radium, { StyleRoot } from "radium";

import Tooltip from "@material-ui/core/Tooltip";
import { withStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";

import RoomRoundedIcon from "@material-ui/icons/RoomRounded";
import { Typography } from "@material-ui/core";

const styles = {
  bounce: {
    animation: "x 3s infinite ease-in-out",
    animationName: Radium.keyframes(bounce, "bounce"),
  },
};

const MyTooltip = withStyles((theme) => ({
  arrow: {
    color: theme.palette.common.black,
  },
  tooltip: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.primary.contrastText,
    boxShadow: theme.shadows[1],
    fontSize: 11,
  },
}))(Tooltip);

const Markers = ({ lat, lng, color, address, id, animate, label }) => {
  const styleType = animate ? styles.bounce : {};
  const [hovering, setHovering] = React.useState(false);
  return (
    <React.Fragment>
      <div lat={lat} lng={lng}>
        <StyleRoot>
          <MyTooltip
            arrow
            placement="top-end"
            title={
              <Box display="flex" p={1} alignContent="center">
                <Box pr={2}>
                  {label && (
                    <Typography variant="caption" color="primary">
                      {`${label}: `}
                    </Typography>
                  )}
                  {address}
                </Box>
              </Box>
            }
          >
            <div
              style={!hovering ? styleType : {}}
              onMouseOver={() => setHovering(true)}
              onMouseEnter={() => setHovering(true)}
              onMouseLeave={() => setHovering(false)}
            >
              <Box color={color || "secondary"}>
                <RoomRoundedIcon
                  fontSize="large"
                  color="inherit"
                  aria-describedby={address && id}
                />
              </Box>
            </div>
          </MyTooltip>
        </StyleRoot>
      </div>
    </React.Fragment>
  );
};

export default Markers;
