import React from "react";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import TextField from "../components/form/TextField";
import AuthBackground from "../components/core/AuthBackground";

import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";

import { Link } from "react-router-dom";
import { emailValidator, passwordValidator } from "../components/core/utils";
import AlertContext from "../components/context/alert/alertContext";
import authContext from "../components/context/auth/authContext";
import { IconButton, InputAdornment } from "@material-ui/core";

const Login = (props) => {
  const { setAlert } = React.useContext(AlertContext);
  const { loading, login, isAuthenticated } = React.useContext(authContext);
  const [email, setEmail] = React.useState("");
  const [password, setPassword] = React.useState("");

  const [passwordVisible, setPasswordVisible] = React.useState(false);
  const _onClick = () => {
    const error = emailValidator(email) || passwordValidator(password);
    if (error) {
      setAlert(error, "error");
    } else {
      login({ email, password });
    }
  };

  React.useEffect(() => {
    isAuthenticated && props.history.push("/dashboard/analytics");
    // eslint-disable-next-line
  }, [isAuthenticated]);

  return (
    <Box width={1} display="flex" flexDirection="row" height={1}>
      <Box
        width={800}
        alignItems="center"
        justifyContent="center"
        height={1}
        display="flex"
        flexDirection="column"
      >
        <Box height={0.25} />
        <Box p={5} m={0} width={0.8}>
          <Box display="flex" justifyContent="center">
            <Typography
              variant="h4"
              color="primary"
              alignText="center"
              gutterBottom
            >
              Sign In
            </Typography>
          </Box>
          <TextField
            id="outlined-basic"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            label="Email"
            required
          />
          <TextField
            id="outlined-basic"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            label="Password"
            type={passwordVisible ? "text" : "password"}
            required
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    onClick={() => setPasswordVisible(!passwordVisible)}
                  >
                    {passwordVisible ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />
          <Button
            variant="contained"
            color="primary"
            size="large"
            fullWidth
            onClick={_onClick}
            disabled={loading}
          >
            {loading ? "Loading..." : "Login"}
          </Button>
          <Box display="flex" justifyContent="flex-end" my={1}>
            <Typography
              variant="body2"
              color="primary"
              align="right"
              style={{ margin: 8, textDecoration: "none" }}
              component={Link}
              to="/forgot"
            >
              Forgot Password
            </Typography>
          </Box>
        </Box>
        <Box
          display="flex"
          justifyContent="center"
          alignItems="flex-end"
          height={0.25}
        >
          <Typography
            variant="body2"
            color="textPrimary"
            align="right"
            style={{ margin: 8, textDecoration: "none" }}
            component={Link}
            to="/register"
          >
            Create an Account
          </Typography>
        </Box>
      </Box>
      <AuthBackground />
    </Box>
  );
};

export default Login;
